// Reset

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

// !Reset

// Variables

:root {
  --color-white: #fff;
  --color-black: #000;
  --color-solitude: #e7f6ff;
  --color-gallery: #eaeaea;
  --color-concrete: #f3f3f3;
  --color-silver: #bdbdbd;
  --color-silver-chalice: #9d9d9d;
  --color-conifer: #9dd558;
}

// !Variables

* {
  font-family: 'Inter', sans-serif;
}

body {
  margin: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-solitude);
  color: var(--color-black);
}

.wrapper {
  background-color: var(--color-white);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  width: 100%;
  margin: 85px auto;
}

header {
  border-bottom: 1px solid var(--color-gallery);

  img {
    margin-right: 15px;
  }
}

.headerInfo {
  h3 {
    font-size: 20px;
    line-height: 24px;
  }
  span {
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    color: var(--color-silver-chalice);
  }
}

.search-block {
  border: 1px solid var(--color-concrete);
  border-radius: 10px;
  padding: 0 15px;
  position: relative;

  .search-block-clear {
    position: absolute;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
    width: 16px;
    height: 16px;
    cursor: pointer;
    z-index: 1000;
  }

  input {
    border: 0;
    padding: 13px;
    font-size: 14px;
    width: 200px;
    outline: transparent;

    &[type='search']::-webkit-search-cancel-button {
      display: none;
    }
  }
}

.cartItem {
  padding: 20px;
  border: 1px solid var(--color-concrete);
  border-radius: 20px;
  overflow: hidden;

  .cartItemImg {
    width: 70px;
    height: 70px;
    background-size: contain;
    background-position: 0 -3px;
    background-repeat: no-repeat;
    margin: 20px;
  }

  span {
    display: block;
    font-size: 14px;
    line-height: 1;
  }
  b {
    font-size: 14px;
  }
}

.removeBtn {
  opacity: 0.5;
  cursor: pointer;
  transition: opacity 0.2s ease-in-out;

  &:hover {
    opacity: 1;
  }
}

.cartTotalBlock {
  margin-top: auto;
  ul {
    padding: 0;
    margin-bottom: 24px;
    li {
      display: flex;
      align-items: flex-end;
      margin-bottom: 20px;

      div {
        flex: 1;
        height: 1;
        border-bottom: 1px dashed var(--color-silver-chalice);
        position: relative;
        top: -4px;
        margin: 0 7px;
      }
    }
  }

  .greenButton {
    position: relative;

    &:disabled {
      animation: button-loading 0.8s ease-in-out infinite;
      background-color: #bebebe !important;
      cursor: default;
    }

    &:hover {
      img {
        transform: translateX(5px);
      }
    }

    img {
      position: absolute;
      right: 33px;
      transition: transform 0.2s ease-in-out;
    }
  }
}

.greenButton {
  position: relative;
  padding: 18px 33px;
  width: 100%;
  border: none;
  border-radius: 18px;
  background-color: var(--color-conifer);
  font-size: 16px;
  font-weight: 600;
  line-height: 1;
  color: var(--color-white);
  transition: background-color 0.3s ease-in-out;
  cursor: pointer;

  &:hover {
    background-color: lighten(#9dd558, 5%);
  }

  &:active {
    background-color: darken(#9dd558, 10%);
  }
}

.cartEmpty {
  text-align: center;

  p {
    width: 280px;
    line-height: 24px;
  }

  .greenButton {
    width: 245px;
    margin-top: 20px;

    &:hover {
      img {
        transform: translateX(-3px);
      }
    }

    img {
      position: relative;
      top: 1px;
      margin-right: 15px;
      transition: transform 0.15s ease-in-out;
    }
  }
}

@keyframes button-loading {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}
